.info_container {
    background-color: rgb(243, 240, 235);
    width: 90%;
    min-width: 420px;
    padding: 35px 50px;
    position: absolute;
    left: 50%;
    top: 55%;

    transform: translate(-50%, -50%);
    border-radius: 10px;
    box-shadow: 20px 30px 25px rgba(0, 0, 0, 0);
    /* display: flex;
    flex-direction: row;
    /* align-items: center;  */
}

h1 {
    font-size: 30px;
    text-align: center;
    color: #1c093c;
}

p {
    position: relative;
    margin: auto;
    width: 100%;
    text-align: center;
    color: #606060;
    font-size: 14px;
    font-weight: 400;

}

form {
    width: 100%;
    position: relative;
    margin: 30px auto 0 auto;
}

.row {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px 30px;
    margin-bottom: 20px;
}

label {
    color: #606060;
    font-size: 14px;

}

input {
    width: 100%;
    font-weight: 400;
    padding: 8px 10px;
    border-radius: 5px;
    border: 1.2px solid #c4cae0;
    margin-top: 5px
}

input:focus {
    outline: none;
    border-color: #6f6df4;
}

button {
    border: none;
    padding: 10px 20px;
    background: linear-gradient(130deg,
            #6f6df4, #4c46f5);
    color: #ffff;
    border-radius: 3px;
}